.page {
  background-color: @color-light;
}

.page__body {
  max-width: 100%;
  margin: 0;
  font-family: @main-font;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: @color-dark-text;
  overflow-x: hidden;
}

.page__title {
  padding-top: 27px;
}

@media (min-width: @desktop-width) {
  .page__title {
    padding-top: 54px;
  }

  .page__img {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 50%;
      height: 695px;
      background-color: @bt-dark;
      z-index: -2;
    }

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 720px;
      top: 0;
      right: 0;
      background-image: url("../img/background-main-screen.jpeg");
      background-repeat: no-repeat;
      background-position: right top;
      z-index: -1;
    }
  }
}
